<template>
  <div id="app">
    <nav>
      <div class="nav-wrap">
        <div class="container">
          <router-link v-if="!authUser" to="/login"><button class="btn">Login</button></router-link>
          <router-link v-if="authUser" to="/"><button class="btn">Main Page</button></router-link>
          <router-link v-if="authUser" to="/chat"><button class="btn">Char Room</button></router-link>
          <button v-if="authUser" v-on:click="signOut" class="btn">Logout</button>
          <router-link v-if="!authUser" to="/register"><button class="btn">Register</button></router-link>
        </div>
      </div>
    </nav>
    <router-view/>
  </div>
</template>

<script>
  import firebase from '@/firebase.js'

export default {
  name: 'App',
  data(){
    return{
      authUser: null
    }
  },
  methods:{
    signOut() {
      firebase.auth().signOut();
    },
  },
  created(){
    firebase.auth().onAuthStateChanged((user) => {
      this.authUser = user;
    })
  }

}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  nav{
    height: 100px;
    background-color: #fdb913;
    padding: 15px 25px;
    color: #2d3743;
  }
</style>



