<template>
  <div id="mainPage">
    <h2>Chat Room</h2>
    <div id="chat">
      <div v-for="message in messages" v-bind:class="{  myMessage: message.senderId == authUser.uid, message }" v-if="message.text">{{message.text}}</div>

    </div>
    <textarea v-model="inputText" cols="30" rows="10"></textarea>
    <button class="btn" id="send" v-on:click="sendMessage">Send</button>
  </div>
</template>

<script>
  import firebase from '@/firebase.js'
  const database = firebase.database();

  export default {
    name: "Chat",
    data(){
      return{
        authUser: null,
        messages: [],
        inputText: ''
      }
    },
    methods:{
      sendMessage() {
        database.ref('chats/A6B1IbtbdQN9spyJ9ohyVTwQaoG3/Office/messages').push().set({date: Math.floor(Date.now() / 1000), senderId: this.authUser.uid, text: this.inputText});
        this.inputText = ''
      }
    },
    created(){
      database.ref('chats/A6B1IbtbdQN9spyJ9ohyVTwQaoG3/Office/messages').on('child_added', snapshot => this.messages.push(snapshot.val()));

      firebase.auth().onAuthStateChanged((user) => {
        this.authUser = user;
      })

    }
  }
</script>

<style scoped>
  #chat{
    margin: 0 auto;
    width: 40%;
    height: 500px;
    border: 2px solid #666;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    border-radius: 25px;
    padding: 15px 25px;
    overflow: auto;
    position:relative;
  }
  .message{
    background-color: #0562E8;
    max-width: 150px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    border-radius: 25px;
    padding: 5px;
    margin-top: 5px;
    color: white;
  }
  textarea{
    margin-top: 15px;
    width: 30%;
  }
  .myMessage{
    margin-left: 550px;
  }

</style>



