<template>
  <div id="registerPage">
    <form @submit.prevent="register">
      <h2>Register page</h2>
      <input type="email" v-model="email" placeholder="email">
      <input type="password" v-model="password" placeholder="password">
      <button type="submit" v-on:click="register" class="btn">Register</button>
    </form>
  </div>
</template>

<script>
    export default {
        name: "Register",
      data(){
          return{
            email: '',
            password: '',
          }
      },
      methods:{
        register () {
          firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then(function () {
            this.email = '';
            this.password = '';
          });
        }
      }
    }
</script>

<style scoped>
  form{
    margin: 0 auto;
    width: 50%;
  }
</style>



